import React, { useState, useEffect } from 'react';
import { useTheme } from '../context/ThemeContext';
import { Card } from '../components/FormElements';

const MarketUpdates = () => {
  const { isDarkMode } = useTheme();
  const [marketUpdates, setMarketUpdates] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchMarketUpdates();
  }, []);

  const fetchMarketUpdates = async () => {
    try {
      setLoading(true);
      
      // Get logs from localStorage
      const storedLogsJSON = localStorage.getItem('steadyGainsLogs');
      
      if (storedLogsJSON) {
        const allLogs = JSON.parse(storedLogsJSON);
        
        // Filter to get only market_update logs and sort by timestamp (newest first)
        const updates = allLogs
          .filter(log => log.type === 'market_update')
          .sort((a, b) => new Date(b.timestamp) - new Date(a.timestamp));
        
        setMarketUpdates(updates);
        setError(null);
      } else {
        // If no logs in localStorage, use mock data
        const mockUpdates = [
          {
            id: 1,
            type: 'market_update',
            timestamp: '2023-06-20T16:30:00',
            title: 'Market Update - June 20, 2023',
            content: 'Markets closed higher today with Nifty up 0.8% and Bank Nifty up 1.2%. FIIs were net buyers while DIIs were net sellers. Global markets were mixed with US futures pointing slightly higher.'
          },
          {
            id: 2,
            type: 'market_update',
            timestamp: '2023-06-19T16:45:00',
            title: 'Market Update - June 19, 2023',
            content: 'Markets traded in a narrow range today with Nifty closing flat. Bank Nifty underperformed with a 0.5% decline. IT and Pharma sectors showed strength while Auto and Metals dragged.'
          },
          {
            id: 3,
            type: 'market_update',
            timestamp: '2023-06-16T17:00:00',
            title: 'Weekly Market Wrap - June 16, 2023',
            content: 'Markets ended the week on a positive note with Nifty gaining 1.2% for the week. Global cues remained supportive with the US Fed signaling a pause in rate hikes. Volatility decreased with India VIX falling 8% week-on-week.'
          }
        ];
        
        setMarketUpdates(mockUpdates);
      }
    } catch (err) {
      console.error('Error fetching market updates:', err);
      setError('Failed to load market updates. Please try again later.');
    } finally {
      setLoading(false);
    }
  };

  // Format timestamp for display
  const formatTimestamp = (timestamp) => {
    const date = new Date(timestamp);
    return date.toLocaleString('en-IN', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  return (
    <div className="max-w-4xl mx-auto">
      <Card title="Market Updates">
        {loading ? (
          <div className={`text-center py-4 ${isDarkMode ? 'text-white' : 'text-neutral-DEFAULT'}`}>
            Loading market updates...
          </div>
        ) : error ? (
          <div className={`text-center py-4 ${isDarkMode ? 'text-accent-red' : 'text-accent-red'}`}>
            {error}
          </div>
        ) : marketUpdates.length === 0 ? (
          <div className={`text-center py-4 ${isDarkMode ? 'text-white' : 'text-neutral-DEFAULT'}`}>
            No market updates available.
          </div>
        ) : (
          <div className="space-y-6">
            {marketUpdates.map((update) => (
              <div 
                key={update.id} 
                className={`p-4 rounded-sm border ${
                  isDarkMode 
                    ? 'border-neutral-light bg-neutral-DEFAULT' 
                    : 'border-neutral-lightest bg-white'
                }`}
              >
                <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center mb-3">
                  <h3 className={`text-lg font-semibold ${isDarkMode ? 'text-white' : 'text-primary-DEFAULT'}`}>
                    {update.title}
                  </h3>
                  <span className={`text-sm mt-1 sm:mt-0 ${isDarkMode ? 'text-neutral-lighter' : 'text-neutral-light'}`}>
                    {formatTimestamp(update.timestamp)}
                  </span>
                </div>
                <p className={`${isDarkMode ? 'text-white' : 'text-neutral-DEFAULT'} whitespace-pre-line`}>
                  {update.content}
                </p>
              </div>
            ))}
          </div>
        )}
      </Card>
    </div>
  );
};

export default MarketUpdates;
